import React from "react";
import { Zcascader, ZcodeHighlight } from "zerod";
import { message, Button } from "antd";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC } = HOC;
import doc from "zerod/components/Zcascader/doc.md";
import jsonmd from "./json.md";
import area from "@/mock/area";
export default AmdDocHOC(doc, {
	demo1: function() {
		class Myjavascript extends React.PureComponent {
			methods = {
				onSelect: items => {
					let names = items.map(item => {
						return item.name;
					});
					message.success(`您选择了${names.join("-")}`);
				},
				showJson: e => {
					e.target._render = () => {
						return (
							<div className="z-panel">
								<div className="z-panel-body">
									<ZcodeHighlight mode="html">{jsonmd}</ZcodeHighlight>
								</div>
							</div>
						);
					};
					e.target._scroll = true;
				},
			};
			render() {
				return (
					<section>
						<div className="z-clear-fix z-margin-bottom-15">
							<Button
								className="z-float-right z-open-modal-btn"
								type="primary"
								onClick={this.methods.showJson}
							>
								查看tree数据结构例子
							</Button>
						</div>
						<Zcascader
							tree={area.data}
							itemKeys={{ name: "name", id: "areaId", disabled: "disable", children: "children" }}
							selections={["1710121748130980000000166"]}
							onSelect={this.methods.onSelect}
						/>
					</section>
				);
			}
		}
		return <Myjavascript />;
	},
	demo2: function() {
		class Myjavascript extends React.PureComponent {
			methods = {
				onSelect: items => {
					let names = items.map(item => {
						return item.name;
					});
					message.success(`您选择了${names.join("-")}`);
				},
				treeAsync: (data, resolve) => {
					if (data.root) {
						setTimeout(() => {
							resolve(area.data);
						}, 500);
					} else if (data.children && data.children.length) {
						setTimeout(() => {
							resolve(data.children);
						}, 500);
					} else {
						resolve([]);
					}
				},
			};
			render() {
				return (
					<Zcascader
						treeAsync={this.methods.treeAsync}
						itemKeys={{ name: "name", id: "areaId", disabled: "disable", children: "children" }}
						selections={["1710121748130980000000166"]}
						onSelect={this.methods.onSelect}
					/>
				);
			}
		}
		return <Myjavascript />;
	},
	demo3: function() {
		class Myjavascript extends React.PureComponent {
			methods = {
				onSelect: items => {
					let names = items.map(item => {
						return item.name;
					});
					message.success(`您选择了${names.join("-")}`);
				},
				showJson: e => {
					e.target._render = () => {
						return (
							<div className="z-panel">
								<div className="z-panel-body">
									<ZcodeHighlight mode="html">{jsonmd}</ZcodeHighlight>
								</div>
							</div>
						);
					};
					e.target._scroll = true;
				},
			};
			selections = ["1710121748130980000000166"];
			itemKeys = { name: "name", id: "areaId", disabled: "disable", children: "children" };
			popoverContentRender = (data, index) => {
				return (
					<>
						<Button type="primary" size="small" onClick={e => e.stopPropagation()}>
							修改
						</Button>
						<Button
							type="danger"
							className="z-margin-left-10"
							size="small"
							onClick={e => e.stopPropagation()}
						>
							删除
						</Button>
					</>
				);
			};
			render() {
				return (
					<section>
						<div className="z-clear-fix z-margin-bottom-15">
							<Button
								className="z-float-right z-open-modal-btn"
								type="primary"
								onClick={this.methods.showJson}
							>
								查看tree数据结构例子
							</Button>
						</div>
						<Zcascader
							tree={area.data}
							itemKeys={this.itemKeys}
							selections={this.selections}
							onSelect={this.methods.onSelect}
							popoverContentRender={this.popoverContentRender}
						/>
					</section>
				);
			}
		}
		return <Myjavascript />;
	},
});
